import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import { CircularProgressbar } from 'react-circular-progressbar';

let all_documents = [
  { name: 'Aadhaar Card Front', doc_name: 'aadhar_card', condition: 'required', is_show: true },
  { name: 'Aadhaar Card Back', doc_name: 'aadhar_card_back', condition: 'required', is_show: true },
  { name: 'Passport Front', doc_name: 'passport', condition: 'optional', is_show: true },
  { name: 'Passport Back', doc_name: 'passport_back', condition: 'optional', is_show: true },
  { name: 'PAN Card', doc_name: 'pancard', condition: 'required', is_show: true },
  { name: 'Driving License', doc_name: 'driving_license', condition: 'optional', is_show: true },
  { name: 'Resume', doc_name: 'resume', condition: 'required', is_show: true },
  { name: 'Passport Size Photo', doc_name: 'passport_size_photo', condition: 'required', is_show: true },
  { name: '10th Marksheet', doc_name: 'secondary_marksheet', condition: 'required', is_show: true },
  { name: '12th Marksheet', doc_name: 'senior_secondary_marksheet', condition: 'optional', is_show: true },
  { name: 'ITI Marksheets & Final Passing Certificate', doc_name: 'ITI_marksheet', condition: 'optional', is_show: true },
  { name: 'Diploma Marksheets & Final Passing Certificate', doc_name: 'diploma_marksheet', condition: 'optional', is_show: true },
  { name: 'Graduation Marksheets & Degree Certificate', doc_name: 'graduation_marksheet', condition: 'optional', is_show: true },
  { name: 'Post Graduation Marksheets & Degree Certificate', doc_name: 'post_graduation_marksheet', condition: 'optional', is_show: true },
  { name: 'Bank’s Cancel Cheque or Passbook', doc_name: 'bank_passbook', condition: 'required', is_show: true },
  { name: 'Previous Company’s Experience Letter', doc_name: 'previous_company_experience_letter', condition: 'optional', is_show: true },
  { name: 'Previous Company’s Payslips', doc_name: 'previous_company_payslip', condition: 'optional', is_show: true },
  { name: 'Police Verification', doc_name: 'police_verification', condition: 'required', is_show: true },
  { name: 'Medical Certificate', doc_name: 'medical_certificate', condition: 'required', is_show: true },
  { name: 'BGV Certificate', doc_name: 'bgv_certificate', condition: 'optional', is_show: true },
];

const DocumentDetails = (props) => {
  const { uploadDocuments, onClickActive } = props
  const [documentFormData, setDocumentFormData] = useState({})
  const [allDocumentData, setAllDocumentData] = useState([])
  const [errors, setErrors] = useState({});

  const percentage = calculateFilledPercentage('');

  useEffect(() => {
    setAllDocumentData(all_documents)
  }, [])

  const handleDocFile = (e, index) => {
    // errors && setErrors({});
    const { name } = e.target
    const file = e.target.files[0]
    setDocumentFormData({ ...documentFormData, [name]: file })
    setAllDocumentData((prev) =>
      prev.map((item, ind) =>
        ind === index ? { ...item, file: URL.createObjectURL(file) } : item
      )
    );
  }

  const onSubmitForm = async () => {
    // let findRequired = allDocumentData && allDocumentData?.filter(data => data.condition == 'required')
    // let findAttachFile = allDocumentData && allDocumentData?.filter(data => data.condition == 'required' && data.file)
    // if(findRequired?.length != findAttachFile?.length){
    //   setErrors({REQUIREDFIELD:`Document`});
    // }else{
    //   setErrors({})
    // }
    uploadDocuments(documentFormData)
  };

  useEffect(() => {
    return () => {
      allDocumentData.forEach((doc) => {
        if (doc.file) {
          URL.revokeObjectURL(doc.file);
        }
      });
    };
  }, [allDocumentData]);

  return (
    <>
      <div className="rightSec">
        <div className="formPanel detailInner">
          <div className="DetailHeader">
            <h6 className="m-0 fw-semibold">Upload Documents</h6>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                {/* <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />; */}
              </div>
            </div>
          </div>
          <div className="p-3">
            <div className="row">
              {allDocumentData && allDocumentData?.length > 0 && allDocumentData.map((doc, index) => (
                <div className="col-md-6 row">
                  <div className={`col-md-${doc.file?9:12}`}>
                    <div className="form-group mb-3">
                      <label htmlFor="">{doc.name}  {doc.condition === 'required' && (
                          <span style={{ color: 'red', fontSize: '15px' }}>
                            *
                          </span>
                        )}</label>
                      <input type="file"
                        id="policyFile"
                        name={doc.doc_name}
                        className={`form-control `}
                        onChange={(e) => handleDocFile(e, index)}
                        accept=".png, .jpeg, .doc, .docx, .pdf"
                      // ref={fileInputRef}
                      />
                    {/* {(errors && doc.condition == 'required' && !doc.file) && 
                    <CustomError
                    text={errors}
                  />} */}
                    </div>
                  </div>
                  <div className='col-md-3'>
                    {doc.file && <img src={doc.file} alt="Preview"
                    />}

                  </div>


                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="pt-3">
          <p className="fw-medium">
            Failure to submit the required documents (Educational, Experience (If
            any), Police Verification, Aadhaar Card with Date of Birth, father’s name
            mentioned on it) till the Date of Joining will declare the extension of
            your date of joining.
          </p>
          <p className="fw-medium">
            {" "}
            I hereby authorize QDegrees or their representative to verify information
            presented on my Employee Form/Resume and procure an investigate report or
            consumer report for that purpose. I certify that the information furnished
            in this form filled-in by me in conjunction with my addresses, details are
            true to the best of my knowledge. I hereby grant authority to the bearer
            of this letter to access or to be provided with full details.
          </p>
          <p className="fw-medium">
            {" "}
            I have thoroughly read above instructions and take full responsibility of
            it.
          </p>
        </div>
        <div className='d-flex' style={{ justifyContent: "space-between" }}>
          <div className="mt-3 mb-3">
            <CustomButton
              text="Back"
              classNames="btn btn-org-gradient"
              handleClick={() => onClickActive(7)}
            />
          </div>
          <div className="mt-3 mb-3">
            <CustomButton
              text={"Save & Next"}
              classNames="btn btn-org-gradient"
              handleClick={() => onSubmitForm()}
            />
          </div>
        </div>
      </div>

    </>
  )
}

export default DocumentDetails