import React, { useEffect, useState } from 'react';
import { OnboardingSidebar, PersonalDetails, FamilyDetails, EducationDetails, BankDetails, SocialDetails, WorkExperienceDetails, ReferenceDetails, DocumentDetails, PolicyDetails } from '../components/employee/onboarding-process'
import { useDispatch, useSelector } from 'react-redux';
import { employeeActions, onboardingActions, policiesActions } from '../core/resource/actions';
const Onboarding = () => {
    const [isActive, setIsActive] = useState(1);
    const [filledFormTabs, setFilledFormTabs] = useState([1])
    const [allPolicyListData, setAllPolicyListData] = useState('')
    const [profileData,setProfileData]=useState('')
    const onClickActive = (val) => {
        setIsActive(val)
        setFilledFormTabs(pre => [...pre, val])
    }
    const dispatch = useDispatch();
    const {
        addPersonalDetailsPending,
        addPersonalDetailsRes,
        addPersonalDetailsError,

        addFamilyDetailsPending,
        addFamilyDetailsRes,
        addFamilyDetailsError,

        addEducationDetailsPending,
        addEducationDetailsRes,
        addEducationDetailsError,

        addBankDetailsPending,
        addBankDetailsRes,
        addBankDetailsError,

        addSocialDetailsPending,
        addSocialDetailsRes,
        addSocialDetailsError,

        addWorkExperienceDetailsPending,
        addWorkExperienceDetailsRes,
        addWorkExperienceDetailsError,

        addReferenceDetailsPending,
        addReferenceDetailsRes,
        addReferenceDetailsError,

        addDocumentDetailsPending,
        addDocumentDetailsRes,
        addDocumentDetailsError,
    } = useSelector(state => state.onboarding);
    const {
        getProfilePending,
        getProfileRes,
        getProfileError,
    } = useSelector(state => state.employee)

    const {
        getPolicyListPending,
        getPolicyListRes,
        getPolicyListError,
    } = useSelector(state => state.policies)

    const addPersonalDetails = (params) => {
        const payload = {
            query_type: "personal_details",
            step_id: "1",
            ...params
        }
        dispatch(onboardingActions.addPersonalDetailsRequest(payload))
    };

    const addFamilyDetails = (params) => {
        const payload = {
            query_type: "family_details",
            step_id: "2",
            ...params
        }
        dispatch(onboardingActions.addFamilyDetailsRequest(payload))
    };
    const addEductionDetails = (params) => {
        const payload = {
            query_type: "education_details",
            step_id: "3",
            ...params
        }
        dispatch(onboardingActions.addEducationDetailsRequest(payload))
    };

    const addBankDetails = (params) => {
        const payload = {
            query_type: "bank_details",
            step_id: "4",
            ...params
        }
        dispatch(onboardingActions.addBankDetailsRequest(payload))
    };

    const addSocialDetails = (params) => {
        const payload = {
            query_type: "social_details",
            step_id: "5",
            ...params
        }
        dispatch(onboardingActions.addSocialDetailsRequest(payload))
    };

    const addReferenceDetails = (params) => {
        const payload = {
            query_type: "reference_details",
            step_id: "7",
            reference_details: [params]
        }
        dispatch(onboardingActions.addReferenceDetailsRequest(payload))
    };

    const addWorkExperienceDetails = (params) => {
        const payload = {
            query_type: "work_experience_details",
            step_id: "6",
            ...params
        }
        dispatch(onboardingActions.addWorkExperienceDetailsRequest(payload))

    };

    const uploadDocuments = (params) => {
        let formData = new FormData()
        formData.append("aadhar_card", params?.aadhar_card || null)
        formData.append("aadhar_card_back", params?.aadhar_card_back || null)
        formData.append("passport", params?.passport || null)
        formData.append("passport_back", params?.passport_back || null)
        formData.append("pancard", params?.pancard || null)
        formData.append("driving_license", params?.driving_license || null)
        formData.append("resume", params?.resume || null)
        formData.append("passport_size_photo", params?.passport_size_photo || null)
        formData.append("secondary_marksheet", params?.secondary_marksheet || null)
        formData.append("senior_secondary_marksheet", params?.senior_secondary_marksheet || null)
        formData.append("ITI_marksheet", params?.ITI_marksheet || null)
        formData.append("diploma_marksheet", params?.diploma_marksheet || null)
        formData.append("graduation_marksheet", params?.graduation_marksheet || null)
        formData.append("post_graduation_marksheet", params?.post_graduation_marksheet || null)
        formData.append("bank_passbook", params?.bank_passbook || null)
        formData.append("previous_company_experience_letter", params?.previous_company_experience_letter || null)
        formData.append("previous_company_payslip", params?.previous_company_payslip || null)
        formData.append("police_verification", params?.police_verification || null)
        formData.append("medical_certificate", params?.medical_certificate || null)
        formData.append("bgv_certificate", params?.bgv_certificate || null)
        formData.append("query_type", "document_details")
        formData.append("step_id", "8")

        dispatch(onboardingActions.addDocumentDetailsRequest(formData))

    };
    useEffect(() => {
        // personal details
        if (!addPersonalDetailsPending) {
            if (addPersonalDetailsError) {
                dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
                return;
            }
            if (addPersonalDetailsRes) {
                if (addPersonalDetailsRes?.code === 200) {
                    onClickActive(2)
                    dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
                    getProfileData()

                }
            }
        };
        // family details

        if (!addFamilyDetailsPending) {
            if (addFamilyDetailsError) {
                dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
                return;
            }
            if (addFamilyDetailsRes) {
                if (addFamilyDetailsRes?.code === 200) {
                    onClickActive(3)
                    dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
                    getProfileData()
                }
            }
        }
        if (!addEducationDetailsPending) {
            if (addEducationDetailsError) {
                dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
                return;
            }
            if (addEducationDetailsRes) {
                if (addEducationDetailsRes?.code === 200) {
                    onClickActive(4)
                    dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
                    getProfileData()
                }
            }
        }
        // bank details
        if (!addBankDetailsPending) {
            if (addBankDetailsError) {
                dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
                return;
            }
            if (addBankDetailsRes) {
                if (addBankDetailsRes?.code === 200) {
                    onClickActive(5)
                    dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
                    getProfileData()
                }
            }
        }
        // social details
        if (!addSocialDetailsPending) {
            if (addSocialDetailsError) {
                dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
                return;
            }
            if (addSocialDetailsRes) {
                if (addSocialDetailsRes?.code === 200) {
                    onClickActive(6)
                    dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
                    getProfileData()
                }
            }
        }
        // social details
        if (!addWorkExperienceDetailsPending) {
            if (addWorkExperienceDetailsError) {
                dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
                return;
            }
            if (addWorkExperienceDetailsRes) {
                if (addWorkExperienceDetailsRes?.code === 200) {
                    onClickActive(7)
                    dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
                    getProfileData()
                }
            }
        }
        // reference details
        if (!addReferenceDetailsPending) {
            if (addReferenceDetailsError) {
                dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
                return;
            }
            if (addReferenceDetailsRes) {
                if (addReferenceDetailsRes?.code === 200) {
                    onClickActive(8)
                    dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
                    getProfileData()
                }
            }
        }

        // document details
        if (!addDocumentDetailsPending) {
            if (addDocumentDetailsError) {
                dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
                return;
            }
            if (addDocumentDetailsRes) {
                if (addDocumentDetailsRes?.code === 200) {
                    onClickActive(9)
                    dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
                     getProfileData()
                }
            }
        }
    }, [
        addPersonalDetailsRes,
        addPersonalDetailsError,
        addPersonalDetailsPending,

        addFamilyDetailsRes,
        addFamilyDetailsError,
        addFamilyDetailsPending,

        addEducationDetailsPending,
        addEducationDetailsRes,
        addEducationDetailsError,


        addBankDetailsRes,
        addBankDetailsError,
        addBankDetailsPending,

        addSocialDetailsRes,
        addSocialDetailsError,
        addSocialDetailsPending,

        addWorkExperienceDetailsPending,
        addWorkExperienceDetailsRes,
        addWorkExperienceDetailsError,

        addReferenceDetailsRes,
        addReferenceDetailsError,
        addReferenceDetailsPending,

        addDocumentDetailsRes,
        addDocumentDetailsError,
        addDocumentDetailsPending,
    ]);

    /* Get All Policy  List*/
    const handleGetPolicyList = () => {
        dispatch(policiesActions.getPolicyListRequest());
    }

    useEffect(() => {
        if (!getPolicyListPending) {
            if (getPolicyListError) {
                return;
            }
            if (getPolicyListRes) {
                if (getPolicyListRes?.code === 200) {
                    const getPolicyListResData = getPolicyListRes?.data
                    setAllPolicyListData(getPolicyListResData)
                    getProfileData()
                }
            }
        }
    }, [
        getPolicyListRes,
        getPolicyListError,
        getPolicyListPending
    ]);

    useEffect(() => {
        handleGetPolicyList()
        getProfileData()
    }, [])


    const getProfileData = () => {
        dispatch(employeeActions.getProfileRequest())
    }

    useEffect(() => {
        if (!getProfilePending) {
            if (getProfileError) {
                return;
            }
            if (getProfileRes) {
                if (getProfileRes?.code === 200) {
                    setProfileData(getProfileRes?.data[0])
                    console.log(getProfileRes.data,"getProfileRes.data----------")
                }
            }
        }
    }, [
        getProfileRes,
        getProfileError,
        getProfilePending
    ]);


    return (
        <>
            <div class="container-fluid">
                <div class="sidebarDoc">
                    <div class="row px-0 ">
                        <OnboardingSidebar onClickActive={onClickActive} isActive={isActive} filledFormTabs={filledFormTabs} />
                        <div class="col-md-9 px-0 mainoverflow">
                            {isActive === 1 && <PersonalDetails addPersonalDetails={addPersonalDetails} onClickActive={onClickActive} 
                            profileData={profileData}
                            />}
                            {isActive === 2 && <FamilyDetails addFamilyDetails={addFamilyDetails} onClickActive={onClickActive} profileData={profileData}/>}
                            {isActive === 3 && <EducationDetails addEductionDetails={addEductionDetails} onClickActive={onClickActive} profileData={profileData}/>}
                            {isActive === 4 && <BankDetails addBankDetails={addBankDetails} onClickActive={onClickActive} profileData={profileData}/>}
                            {isActive === 5 && <SocialDetails addSocialDetails={addSocialDetails} onClickActive={onClickActive}  profileData={profileData}/>}
                            {isActive === 6 && <WorkExperienceDetails addWorkExperienceDetails={addWorkExperienceDetails} onClickActive={onClickActive} profileData={profileData}/>}
                            {isActive === 7 && <ReferenceDetails addReferenceDetails={addReferenceDetails} onClickActive={onClickActive} profileData={profileData}/>}
                            {isActive === 8 && <DocumentDetails uploadDocuments={uploadDocuments} onClickActive={onClickActive} />}
                            {isActive === 9 && <PolicyDetails allPolicyListData={allPolicyListData} />}
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Onboarding